import React from 'react'
import Image from '../util/Image'
import splash from "../../images/Elm Splash.png"
import mainimage from "../../images/AGDII Portfolio 01.png"

const Elm = () => {
    return (
        <div className="border w-50 inline-flex">
            <div>
                <Image image={splash} />
            </div>
            <div>
                <Image image={mainimage} />
            </div>
        </div>
    )
}

export default Elm
