import React from "react";
import mainimage from "../images/AGDII Portfolio 01.png";
import Image from "./util/Image";
import { NavLink } from "react-router-dom";

const Projects = () => {
  return (
    <div className="bg-red-100">
      <div className="w-1/2 bg-black-900 text-center">
        <NavLink to="/elm">
            <div className="relative">
                <Image image={mainimage} />
                {/* <div className="absolute border-black text-4xl inset-1/2 text-green">Elm Game</div> */}
            </div>
        </NavLink>
      </div>
    </div>
  );
};

export default Projects;
